<template>
  <div class="list">
    <h2>全部訂單</h2>
	<Seach/>
    <router-link v-for="(item,index) in list.data" :to="'/OrderDetails/'+item.Id">
      <div class="list-d order-list">
        <el-row>
          <el-col :xl="4" :md="4" :sm="4" :xs="4">
            <div>
              <div>訂單編號:{{item.SOrderNo}}</div>
              <div>預約時間:{{item.ReservationDate}}</div>
              <div>狀態:{{GLOBAL.status_arr[item.Status]}}</div>
              <div>行程:{{item.IsBack ? "回程" : "去程"}}</div>
              <div>建立時間:{{item.CreateTime}}</div>
            </div>
          </el-col>
          <el-col :xl="16" :md="16" :sm="16" :xs="16"></el-col>
          <el-col :xl="4" :md="4" :sm="4" :xs="4">
            <div>
              <div>狀態:{{GLOBAL.status_arr[item.Status]}}</div>
              <div>個案負擔 ${{item.SelfPayAmt+item.OtherAmt}}</div>
              <div>(車資 + 陪同金額)</div>
              <div class="bt">查看金額</div>
              <div class="bt">訂單歷程</div>
            </div>
          </el-col>
        </el-row>
      </div>
	</router-link>
    <div class="no-found" v-if="list.data&&list.data.length<1">查無資料</div>
	<Page :max="list.pageCount" :url="'OrderList'"/>
  </div>
</template>

<script>
import Seach from './Seach'
import Page from './Page'
export default {
  data() {
    return {
	  list:[],
	}
  },
  components: {Page,Seach},
  mounted:async function() {
    this.GLOBAL.show_black=true
	await this.all_ready()
    await this.get_data(this.$route.params.page)
	console.log(this.list)
	this.GLOBAL.show_black=false
  },
  watch:{
    async $route(to, from){
	  this.GLOBAL.show_black=true
      await this.get_data(this.$route.params.page)
	  this.GLOBAL.show_black=false
    }
  },
  methods:{
    get_data:async function(x) {
	  this.list=await this.Function.get_OrderDetails(x)
	  console.log(this.list)
	}
  }
}
</script>



